import { Component, OnInit } from '@angular/core';
import { KisanPortalService } from '../kisan-portal.service';

@Component({
  selector: 'app-store-location',
  templateUrl: './store-location.component.html',
  styleUrls: ['./store-location.component.css']
})
export class StoreLocationComponent implements OnInit {
  errorMsg: any = 'Unable to Save Location Details, Please Try again.';
  successMsg: any = 'Location Details Saved Successfully.';
  errorFlag: boolean = false;
  successflag: boolean = false;
  location = {
    locName: "",
    locType: "",
    description: "",
    addressDetails: {
      addressLine1: "",
      addressLine2: "",
      addressLine3: "",
      city: "",
      state: "",
      pin: 0,
      emain: "",
      mobileNum: 0,
      altMobileNum: 0,
      residencePhNum: 0,
      officePhNum: 0,
      addressType: ""
    },
    registrationInfo: {
      pan: "",
      tin: "",
      gst: "",
      cst: "",
      licenceNum: ""
    },
    accountNum: "",
    accountName: "",
    bankName: "",
    ifscCode: ""
  }

  constructor(private kisanPortalService: KisanPortalService) { }

  ngOnInit() {
  }
  saveLocation() {
    debugger;
    console.log("Location Data : " + this.location);
    this.kisanPortalService.saveLocation(this.location).subscribe(
      (data) => {
        console.log(data);
        this.errorFlag = false;
        this.successflag = false;
        this.successflag = true;
        this.clearlocationData();
        this.successMsg = "Location Details are Saved Successfully.";
      },
      Error => {
        this.successflag = false;
        this.errorFlag = true;
        this.errorMsg = "Unable to Save Location Details, Please Try again.";
      });
  }
  clearlocationData() {
    this.location = {
      locName: "",
      locType: "",
      description: "",
      addressDetails: {
        addressLine1: "",
        addressLine2: "",
        addressLine3: "",
        city: "",
        state: "",
        pin: 0,
        emain: "",
        mobileNum: 0,
        altMobileNum: 0,
        residencePhNum: 0,
        officePhNum: 0,
        addressType: ""
      },
      registrationInfo: {
        pan: "",
        tin: "",
        gst: "",
        cst: "",
        licenceNum: ""
      },
      accountNum: "",
      accountName: "",
      bankName: "",
      ifscCode: ""
    }
  }
}
