import { Component, OnInit } from '@angular/core';
import { KisanPortalService } from '../kisan-portal.service';

@Component({
  selector: 'app-kisan-supplier',
  templateUrl: './kisan-supplier.component.html',
  styleUrls: ['./kisan-supplier.component.css']
})
export class KisanSupplierComponent implements OnInit {
  errorMsg: any = 'Unable to Save Supplier Details, Please Try again.';
  successMsg: any = 'Supplier Details Saved Successfully.';
  errorFlag: boolean = false;
  successflag: boolean = false;
  addressCheckFlag: boolean = false;
  supplier = {
    supplierId: 0,
    supplierName: "",
    supplierType: "",
    userId: "",
    vehicle: "",
    addressDetails: {
      addressLine1: "",
      addressLine2: "",
      addressLine3: "",
      city: "",
      state: "",
      pin: 0,
      emain: "",
      mobileNum: 0,
      altMobileNum: 0,
      residencePhNum: 0,
      officePhNum: 0,
      addressType: ""
    },
    registrationInfo: {
      pan: "",
      tin: "",
      gst: "",
      cst: "",
      licenceNum: ""
    },
    deliveryaddressDetails: {
      addressLine1: "",
      addressLine2: "",
      addressLine3: "",
      city: "",
      state: "",
      pin: 0,
      emain: "",
      mobileNum: 0,
      altMobileNum: 0,
      residencePhNum: 0,
      officePhNum: 0,
      addressType: ""
    }

  }
  constructor(private kisanPortalService: KisanPortalService) { }

  ngOnInit() {
  }
  saveSupplier() {
    debugger;
    console.log("supplier Data : " + this.supplier);
    this.kisanPortalService.saveSupplier(this.supplier).subscribe(
      (data) => {
        console.log(data);
        this.errorFlag = false;
        this.successflag = false;
        this.successflag = true;
        this.clearSupplierData();
        this.successMsg = "supplier Details are Saved Successfully.";
      },
      Error => {
        this.successflag = false;
        this.errorFlag = true;
        this.errorMsg = "Unable to Save supplier Details, Please Try again.";
      });
  }
  clearSupplierData() {
    this.supplier = {
      supplierId: 0,
      supplierName: "",
      supplierType: "",
      userId: "",
      vehicle: "",
      addressDetails: {
        addressLine1: "",
        addressLine2: "",
        addressLine3: "",
        city: "",
        state: "",
        pin: 0,
        emain: "",
        mobileNum: 0,
        altMobileNum: 0,
        residencePhNum: 0,
        officePhNum: 0,
        addressType: ""
      },
      registrationInfo: {
        pan: "",
        tin: "",
        gst: "",
        cst: "",
        licenceNum: ""
      },
      deliveryaddressDetails: {
        addressLine1: "",
        addressLine2: "",
        addressLine3: "",
        city: "",
        state: "",
        pin: 0,
        emain: "",
        mobileNum: 0,
        altMobileNum: 0,
        residencePhNum: 0,
        officePhNum: 0,
        addressType: ""
      }

    }
  }
  addressCheck() {
    debugger;
    console.log(this.addressCheckFlag);
    if (this.addressCheckFlag == true) {
      this.supplier.deliveryaddressDetails = this.supplier.addressDetails;
    } else {
      this.supplier.deliveryaddressDetails = {
        addressLine1: "",
        addressLine2: "",
        addressLine3: "",
        city: "",
        state: "",
        pin: 0,
        emain: "",
        mobileNum: 0,
        altMobileNum: 0,
        residencePhNum: 0,
        officePhNum: 0,
        addressType: ""
      }
    }
  }
}
